	module.exports = {"data":{"post":{"html":"<p>Only <a href=\"http://moorbek.weblogs.com/\">one</a> of the German bloggers I read claims to be following the World Cup. Those who mention it at all tend to  snicker at the colleagues/classmates/family who waste time with such things. To follow the national team is like an American saying he watches the Super Bowl. Or, an even better comparison, like an American who follows the Olympics and cheers for the USA. So I won't say anything about Germany-Ireland, except to say I now (along with most of my co-workers) have 90 minutes of work time to make up somehow. And that Ireland's tying goal was deserved.</p>","fields":{"title":"World Cup Germany-Ireland","date":"05 Jun 2002","url":"https://papascott.de/2002-06-05-world-cup-germany-ireland/"}}},"pathContext":{"slug":"/2002-06-05-world-cup-germany-ireland/","prev":{"fields":{"title":"Mozilla 1.0","date":"05 Jun 2002","slug":"/2002-06-05-mozilla-10/"}},"next":{"fields":{"title":"Good Company","date":"06 Jun 2002","slug":"/2002-06-06-good-company/"}}}}

